.App {
  /* text-align: center; */
  height: 100vh;
  box-sizing: border-box;
  overflow-y: auto;
  background-color:  rgba(123, 178, 180, 0.713);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img {
  max-width: 100%;
}

table, td, th {
  border: solid 1px;
  border-collapse: collapse;
  padding: 0px 5px;
}

#preview{
  padding:1%;
}